"use client";
import { useEffect, useState } from "react";

export default function DashboardPage() {
  const [tasks, setTasks] = useState([]);
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [message, setMessage] = useState("");
  const [editId, setEditId] = useState(null);

  const token = localStorage.getItem("token");

  const fetchTasks = async () => {
    try {
      const res = await fetch("http://localhost:5000/api/tasks", {
        headers: { Authorization: `Bearer ${token}` },
      });
      const data = await res.json();
      if (!res.ok) setMessage(data.error || "Failed to fetch tasks");
      else setTasks(data);
    } catch (err) {
      console.error(err);
      setMessage("Cannot reach backend!");
    }
  };

  useEffect(() => {
    if (!token) {
      window.location.href = "/login";
      return;
    }
    fetchTasks();
  }, []);

  const handleAddOrUpdate = async (e) => {
    e.preventDefault();

    try {
      const url = editId
        ? `http://localhost:5000/api/tasks/${editId}`
        : "http://localhost:5000/api/tasks";
      const method = editId ? "PUT" : "POST";

      const res = await fetch(url, {
        method,
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify({ title, description }),
      });

      const data = await res.json();

      if (!res.ok) {
        setMessage(data.error || "Action failed");
        return;
      }

      setTitle("");
      setDescription("");
      setEditId(null);
      fetchTasks();
    } catch (err) {
      console.error(err);
      setMessage("Cannot reach backend!");
    }
  };

  const handleDelete = async (id) => {
    try {
      const res = await fetch(`http://localhost:5000/api/tasks/${id}`, {
        method: "DELETE",
        headers: { Authorization: `Bearer ${token}` },
      });

      if (!res.ok) {
        const data = await res.json();
        setMessage(data.error || "Delete failed");
        return;
      }

      fetchTasks();
    } catch (err) {
      console.error(err);
      setMessage("Cannot reach backend!");
    }
  };

  const handleEdit = (task) => {
    setTitle(task.title);
    setDescription(task.description);
    setEditId(task._id);
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-200 via-blue-200 to-green-200 p-6">
      <h1 className="text-3xl font-bold text-center mb-6 text-purple-700">
        Dashboard
      </h1>

      {/* Add / Edit Task Form */}
      <form
        onSubmit={handleAddOrUpdate}
        className="bg-white p-6 rounded shadow-md w-full max-w-md mx-auto mb-6 flex flex-col gap-4"
      >
        <h2 className="text-xl font-bold text-center text-gray-700">
          {editId ? "Edit Task" : "Add Task"}
        </h2>
        <input
          type="text"
          placeholder="Title"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="border p-2 rounded focus:outline-purple-500"
          required
        />
        <textarea
          placeholder="Description"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          className="border p-2 rounded focus:outline-purple-500"
          required
        />
        <button
          type="submit"
          className="bg-purple-600 text-white py-2 rounded hover:bg-purple-700 transition-colors"
        >
          {editId ? "Update Task" : "Add Task"}
        </button>
        {editId && (
          <button
            type="button"
            className="bg-gray-400 text-white py-2 rounded hover:bg-gray-500 transition-colors"
            onClick={() => {
              setEditId(null);
              setTitle("");
              setDescription("");
            }}
          >
            Cancel Edit
          </button>
        )}
      </form>

      {message && (
        <p className="text-center text-red-600 mb-4 animate-pulse">{message}</p>
      )}

      {/* Task List */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {tasks.map((task) => (
          <div
            key={task._id}
            className="bg-white p-4 rounded shadow hover:shadow-lg transition-shadow"
          >
            <h3 className="font-bold text-lg text-gray-800">{task.title}</h3>
            <p className="text-gray-600">{task.description}</p>
            <div className="mt-4 flex justify-between">
              <button
                onClick={() => handleEdit(task)}
                className="bg-yellow-500 text-white px-3 py-1 rounded hover:bg-yellow-600 transition-colors"
              >
                Edit
              </button>
              <button
                onClick={() => handleDelete(task._id)}
                className="bg-red-500 text-white px-3 py-1 rounded hover:bg-red-600 transition-colors"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
